body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 20px;
  background-color: lightskyblue;
  color: #009dff;
}

.container{
  background: #fff;
  border-radius: 0.25rem;
  box-shadow: 0 5px 15px rgb(0 0 0 / 25%);
  padding: 2rem 0;
  width: 50vw;
  margin: 0 auto;
  min-width: 35rem;
  margin-top: 8rem;
  display: grid;
  justify-content: center;
  text-align: center;
}
form{
  width: 15rem;
}
form *{
  display: block;
}
.temp-img{
  display: grid;
  grid-template-columns: repeat(2,1fr);
}
.temp{
  font-size: 30px;
}
img{
  width: 100px;
  height: 100px;
  margin: auto;
}  

input[type="text"] {
  height: 4vh;
  outline: 2px solid lightskyblue;
  width: 100%;
  border-radius: 5px;
  border: 0px;
  margin-bottom: 15px;
}
input[type="text"]:focus {
  height: 4vh;
  outline: 2px solid lightskyblue;
  width: 100%;
  border-radius: 5px;
  border: 0px;
  margin-bottom: 15px;
}  
button {
  background-color: lightskyblue;
  border: none;
  color: #fff;
  cursor: pointer;
  height: 5vh;
  outline: none;
  border-radius: 5px;
  margin: auto;
}
.List {
  box-shadow: 0 5px 15px rgb(173 216 230);
  margin-top: 20px;
  border-radius: 25px;
  transition: 0.5s;
}
.List:hover{
  transform: scale(1.1) rotateY(360deg);
}